@font-face {
    font-family: "GeeksNormal";
    src: url("../../../../assets/font/GEEKS\ Bet\ \(Normal\).ttf")
        format("truetype");
}

@font-face {
    font-family: "GeeksRoman";
    src: url("../../../../assets/font/GEEKS\ OzHandicraft\ Win95BT\ \(Roman\).ttf")
        format("truetype");
}

@font-face {
    font-family: "Mogul";
    src: url("../../../../assets/font/Mogul\ Futuris.ttf") format("truetype");
}

@font-face {
    font-family: "MogulBold";
    src: url("../../../../assets/font/Mogul\ Futuris\ Bold.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulBoldItalic";
    src: url("../../../../assets/font/Mogul\ Futuris\ Bold\ Italic.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulItalic";
    src: url("../../../../assets/font/Mogul\ Futuris\ Italic.ttf")
        format("truetype");
}

@font-face {
    font-family: "MogulShadow";
    src: url("../../../../assets/font/Mogul\ FuturisShadow.ttf")
        format("truetype");
}

.footerContainer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    background-color: rgb(44, 61, 81);
    color: white;
    padding: 2%;
    min-height: 230px;
}

.footerSecondContainer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

.footerDescription {
    min-width: 260px;
    max-width: 300px;
    font-size: 12px;
    text-align: end;
    font-family: "Mogul";
}
.footerLogo {
    min-width: 100px;
    max-width: 250px;
    max-height: 150px;
    font-family: "Mogul";
    margin: 0 1rem 0 1rem;
}

.ul > li {
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
}

.ul {
    list-style-type: none;
    padding-inline-start: 0;
    min-width: 300px;
    margin-block-start: 0;
    margin-block-end: 0;
    margin: auto;
}

.footerHeaderIcon {
    fill: #82d900 !important;
    cursor: pointer !important;
    margin-right: 5px !important;
    margin-top: 0.2rem;
}

.footerLogoLinkContainer {
    display: flex;
    flex-direction: row;
}

@media only screen and (max-width: 1100px) {
    .footerDescription {
        width: 100%;
        text-align: center;
    }

    .footerSecondContainer {
        flex-direction: column;
    }
    .ul {
        min-width: 0px;
    }
    .footerLogo {
        min-width: 50px;
        max-height: 100px;
    }
}

@media only screen and (max-width: 400px) {
    .footerSecondContainer {
        flex-direction: column;
    }

    .footerLogo {
        min-width: 50px;
        max-height: 100px;
    }
}
